ul.addonify-vs-attributes-options li .adfy-vs {
  display: block;
  border: 1px solid white;
}

ul.addonify-vs-attributes-options li.selected .adfy-vs {
  border-color: red;
}

.addonify-vs-archive-align-left ul.addonify-vs-attributes-options {
  justify-content: flex-start;
}

.addonify-vs-archive-align-center ul.addonify-vs-attributes-options {
  justify-content: center;
}

.addonify-vs-archive-align-right ul.addonify-vs-attributes-options {
  justify-content: flex-end;
}

.addonify-vs-attributes-style-square ul.addonify-vs-attributes-options li,
.addonify-vs-attributes-style-square ul.addonify-vs-attributes-options li .adfy-vs {
  border-radius: 2px;
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  -ms-border-radius: 2px;
}

.addonify-vs-attributes-style-rounded ul.addonify-vs-attributes-options li,
.addonify-vs-attributes-style-rounded ul.addonify-vs-attributes-options li .adfy-vs {
  border-radius: 100%;
  -webkit-border-radius: 100%;
  -moz-border-radius: 100%;
  -ms-border-radius: 100%;
}

ul.addonify-vs-attributes-options-button li .adfy-vs {
  text-align: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  border-color: black;
}

ul.addonify-vs-attributes-options.addonify-vs-attributes-options-button li.selected .adfy-vs {
  border-color: red;
}

ul.addonify-vs-attributes-options-image li .adfy-image-vs {
  margin: 0 !important;
}

.addonify-vs-disabled-blur_with_cross ul.addonify-vs-attributes-options li.disabled {
  background: url(../../images/disabled-bg.svg) no-repeat 50%;
  background-size: 100%;
}

.addonify-vs-disabled-blur_with_cross ul.addonify-vs-attributes-options li.disabled .adfy-vs {
  opacity: 0.4;
}

.addonify-vs-disabled-blur_without_cross ul.addonify-vs-attributes-options li.disabled {
  opacity: 0.5;
}

.addonify-vs-attributes-options-select.hide,
.addonify-vs-attributes-options-select.hidden {
  display: none !important;
}

.variations_form {
  margin: 0;
}

table.variations {
  margin: 10px 0 10px 0;
  border-spacing: 0;
  width: 100%;
  border: none;
}

table.variations td {
  padding: 0;
  border: none;
  vertical-align: unset;
  background-color: transparent !important;
}

table.variations td.label {
  display: none !important;
}

table.variations ul.addonify-vs-attributes-options {
  line-height: 1;
  margin: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  align-content: center;
}

table.variations ul.addonify-vs-attributes-options,
table.variations ul.addonify-vs-attributes-options li {
  margin: 0;
  padding: 0;
  list-style: none;
}

table.variations ul.addonify-vs-attributes-options li {
  display: inline-block;
  line-height: 1;
  font-weight: normal;
  margin: 0;
  padding: 0;
  list-style: none;
  cursor: pointer;
  overflow: hidden;
}

table.variations ul.addonify-vs-attributes-options li.disabled {
  cursor: no-drop;
  opacity: 0.4;
}

table.variations ul.addonify-vs-attributes-options li {
  margin-right: 5px;
}

table.variations ul.addonify-vs-attributes-options li:last-child {
  margin-right: 0;
}

.tippy-box[data-animation=scale][data-placement^=top] {
  transform-origin: bottom;
}

.tippy-box[data-animation=scale][data-placement^=bottom] {
  transform-origin: top;
}

.tippy-box[data-animation=scale][data-placement^=left] {
  transform-origin: right;
}

.tippy-box[data-animation=scale][data-placement^=right] {
  transform-origin: left;
}

.tippy-box[data-animation=scale][data-state=hidden] {
  transform: scale(0.5);
  opacity: 0;
}

.tippy-box {
  font-size: 16px;
  line-height: 1.3;
  font-weight: normal;
  padding: 2px 10px 3px 10px;
  border-radius: 2px;
}

body.single-product table.variations ul.addonify-vs-attributes-options li {
  margin-right: 10px;
}

body.single-product table.variations ul.addonify-vs-attributes-options li:last-child {
  margin-right: 0;
}

body.single-product div.product table.variations tbody tr td.value {
  margin-bottom: 15px;
}